h5 {width:80%; 
    margin:0 auto; 
    padding:80px 50px 80px 0 !important;}

.table {
    width:80%;
    margin:0 auto;
}

.cartcount {
    padding:8px 8px 8px 25px !important;
}

.table > thead > tr > th {
    padding:8px 15px 10px 15px !important;
}

.table > tbody > tr > td > a > img {
    width:100px !important;
    height:115px !important;
}

.table > tbody > tr > td {
    text-align:left !important;
}

.btn-Outline-success {
    height: 55px;
    margin-top:0px;
    margin-right:30px !important;
    width:200px !important;
    background-color: #dc2832;
    border-radius: 4px;
    border: 1px solid#dc2832;
    color: #fff;
    text-align: center;
    font-weight: 600;
    font-size: 14px;
}

.btn-outline-warning {
    margin-right:30px !important;
}

.btn-outline-primary {
    margin-left:10%;
    margin-top:2%;
}