.brand_popup {
    position: absolute;
    z-index: 10;
    top: 50px;
    right: 0;
    left: 0;
    height: 460px;
    width: 1150px;
    padding: 30px;
    background-color: #fff;
    border: 1px solid#000;
}

.Bpopup_img {
    float: left;
    width: 286px;
    height: 410px;
}

.Bpopup_imgbox {
    width: 128px;
    height: 163px;
    float: left;
    margin-left: 3px;
    position: relative;
}

.Bpopup_imgbox:hover {
    cursor: pointer;
}

.Bpopup_imgbox>img {
    width: 100%;
    height: 100%;
}

.Bpopup_text {
    font-size: 15PX;
    color: #fff;
    text-align: center;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 0;
    right: 0;
}

.Bpopup_text:hover {
    text-decoration: underline;
}
.nav_brand > .Bpopup_list {
    float: left;
    width:850px;
    background-color: green;
}

.Bpopup_listBox {
    border-left:1px solid#eee;
    width: 158px;
    height:400px;
    float: left;
}

.Bpopup_listBox>li {
    text-align: left;
    font-size: 13px;
    display: block;
    color: #000;
    padding: 7px 0 7px 20px;
}

.Bpopup_listBox>li:hover {
    font-weight: bold;
    background-color: #f9f9f9;
    cursor: pointer;
}