* {
  margin: 0;
  box-sizing: border-box;
}

ul,
li,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #000;
}

a:hover {
  color: #000;
}

/* ---------헤더--------- */
#header {
  width: 100%;
  height: 184px;
}

#headerTop {
  width: 100%;
  height: 36px;
  padding: 0 10%;
}

.headerTop_menu {
  float: right;
  width: 200px;
  height: 21px;
}

.headerTop_menu>li {
  float: left;
  color: #b2b2b2;
  font-size: 12px;
  line-height: 33px;
}

.headerTop_menu>li>a {
  color: #b2b2b2;
  display: inline-block;
  margin: 0 10px;
}

#headerBottom {
  width: 100%;
  min-height: 96px;
  padding: 0 0 0 17%;
}

.logo {
  width: 183px;
  height: 36px;
  margin-top: -1px;
}

.heder_searchform {
  width: 350px;
  height: 30px;
  border: none;
  position: relative;
  border-bottom: 1px solid #000;
  margin: 1.5% 0 0 3%;
}

.serch {
  position: absolute;
  z-index: 3;
  background-image: url("../public/img/icon_search.png");
  background-size: cover;
  background-repeat: no-repeat;
  width: 24px;
  height: 20px;
  border: 0;
  margin-left: -30px;
  margin-top: 1.5%;
  background-color: #fff;
}

.headerBottom_menu {
  float: right;
  padding-top: 25px;
  right: 12%;
  width: 198px;
  height: 41px;
  position: relative;
}

.headerBottom_menu>li {
  float: left;
}

.headerBottom_menu> li > a {
  margin-left: 10px;
  font-size: 12px;
  color: #000;
}

.menuicon {
  width: 19px;
  height: 20px;
  margin-top: -5px;
  margin-left: 8px;
  color: #555;
}

.menu_arrow {
  width: 6px;
  height: 4px;
  margin: 0 7px;
  color: #555;
}

.mypage_inner_hide {
  display: none;
}

.mypage_inner {
  width: 157px;
  height: 144px;
  padding: 20px;
  background-color: #fff;
  border: 1px solid #000;
  position: absolute;
  margin: 8px 0 0 -50px;
  z-index: 5;
  top: 50px;
  left: 0;
}

.mypage_inner>li {
  font-size: 13px;
  color: #757575;
  line-height: 27px;
}

#nav {
  width: 100%;
  height: 52px;
  padding: 0 15%;
  border-top: 1px solid#eee;
  border-bottom: 1px solid#eee;
}

.nav_brand {
  width: 103px;
  height: 100%;
  float: left;
  text-align: center;
  padding-top: 13px;
  position: relative;
}

.brand>span {
  font-size: 15px;
}

.brand>img {
  width: 6px;
  height: 4px;
  margin-left: 6px;
}

.navul {
  float: left;
}

.navul>li {
  float: left;
  margin: 0 20px;
  height: 52px;
}

.navul>li>a {
  font-size: 14px;
  color: #000;
  height: 100%;
  width: 100%;
  line-height: 52px;
  display: block;
}

.navul>li>a:hover {
  border-bottom: 3px solid#000;
}



/* ---------슬라이더--------- */
.w-100 {
  object-fit: cover;
  height: 700px;
  margin: 0 auto;
}

.carousel-caption {
  top: 20% !important;
  right: 80% !important;
  width: 800px !important;
  text-align: left !important;
}

.carousel-caption>h6 {
  font-size: 24px !important;
  font-weight: 400 !important;
  margin-bottom: 50px !important;
}

.carousel-caption>h2 {
  font-size: 60px !important;
  letter-spacing: -3px !important;
  margin-bottom: 50px;
}

.carousel-caption>p {
  font-size: 18px;
}



/* ---------섹션1--------- */

.section1_title {
  margin: 100px 0 30px 0;
  font-weight: 400;
  text-align: center !important;
}

.mySwiper {
  width: 1240px;
  margin: 0 auto;
  height: 514px;
}

.box_wrap {
  float: left;
  width: 295px;
  margin-right: 20px;
}

.box_wrap img:hover {
  cursor: pointer;
}

.box_wrap img {
  width: 100%;
}

.box_wrap:last-child {
  margin-right: 0;
}

.box_wrap_txtbox>p:hover {
  text-decoration: underline;
  cursor: pointer;
}

.box_wrap_txtbox>p {
  font-size: 13px;
  margin-bottom: 0;
}



.box_wrap_txtbox>p:first-child {
  margin-top: 8px;
  font-size: 12px;
}

.box_wrap_txtbox>span:nth-child(3) {
  color: red;
  margin-right: 10px;
}

.box_wrap_txtbox>span:nth-child(4) {
  color: #ccc;
  font-size: 14px;
  text-decoration: line-through;
  margin-right: 10px;
}

.box_wrap_txtbox>span:last-child {
  font-size: 13px;
  font-weight: bold;
}

.css-1gsv261 {
  border-bottom: 0 !important;
}

.MuiTabs-flexContainer {
  justify-content: center !important;
}

.MuiButtonBase-root {
  color: #000 !important;
  font-size: 15px !important;
}

/* 화살표 css */
.swiper-button-prev {
  width: 28px !important;
  height: 54px !important;
  color: #000 !important;
  background-color: rgba(205, 205, 205, 0.36);
  top: 40% !important;
  left: 0 !important;
  border-radius: 0 50px 50px 0;
}

.swiper-button-prev:hover {
  background-color: #fff;
}

.swiper-button-prev:after {
  font-size: 20px !important;
  margin-right: 8px;
}

.swiper-button-next {
  width: 28px !important;
  height: 54px !important;
  color: #000 !important;
  background-color: rgba(205, 205, 205, 0.36);
  top: 40% !important;
  right: 0 !important;
  border-radius: 50px 0 0 50px;
  z-index: 100000;
}

.swiper-button-next:hover {
  background-color: #fff;
}

.swiper-button-next:after {
  font-size: 20px !important;
  margin-left: 8px;
}




