.TOPbutton {
  width: 51px;
  height: 51px;
  position: absolute;
  bottom: 150px;
  right: 240px;
  z-index: 10;
  cursor: pointer;
}

.start {
  transform: scale(0);

}

.end {
  transform: scale(1);
  transition: all 0.5s;
}