.D_ptoduct {
    width:1240px;
    height:750px;
    display:flex;
    margin:0 auto;
    padding-top:50px;
}

.D_productLeft {
    width:440px;
    height:560px;
    margin-left:80px;
}

.D_productLeft > img {
    width:100%;
    height:100%;
}

.D_productRight {
    float:right;
    width:600px;
    height:700px;
    margin-left:80px;
}

.D_brandbox {
    height:20px;
    display:flex;
    margin-bottom:15px;
}

.D_brandbox img {
    width:13px;
    height:11px;
    margin-top:7px;
    margin-right:3px;
}

.D_brandbox > p {
    font-size:14px;
    font-weight:600;
    letter-spacing:-0.5px;
    margin-top:1.5px;
}

.D_titlebox p {
    font-size:21px;
    display: inline;
    font-weight:500;
    letter-spacing:-0.5px;
}

.D_titlebox img {
    float:right;
    width:30px;
    height:30px;
    margin:0 10px 10px 10px;
    cursor: pointer;
}

.D_price {
    width:560px;
    height:45px;
    margin-top:25px;
}

.D_price p {
    display:inline-block;
    font-size:28px;
    font-weight:bold;
    margin-right:15px;
}

.D_price p:first-child {
    color:#dc2832;
}

.D_price p:nth-child(2) {
    color:#bdbdbd;
    text-decoration:line-through;
    font-weight:400;
}

.D_benefit {
    display:flex;
    justify-content: space-between;
    width:520px;
    height:30px;
    background-color:#f9f9f9;
    margin-top:15px;
    margin-bottom:20px;
    padding:10px 20px 30px 20px;
    border-radius:2px;
}

.D_benefit span {
    font-size:14px;
}

.D_benefit img {
    width:8px;
    height:12px;
    margin-top:5px;
}

.D_mileage {
    width:520px;
    height:40px;
    padding-top:13px;
    border-bottom:1px solid#eee;
}

.D_mileage img {
    width:20px;
    height:20px;
    margin-right:9px;
    margin-top:-3px;
}

.D_mileage span {
    font-size:14px;
}

.D_shipping {
    width:560px;
    height:120px;
    display:flex;
    padding-top:15px;
}

.shipping_left img {
    width:20px;
    height:20px;
    margin-right:12px;
    margin-top:18px;
}

.shipping_right > ul {
    width:518px;
}

.shipping_right > ul> li {
    width:490px;
    margin:15px 0;
}

.shipping_right > ul> li > img {
    width:55px;
    height:12px;
    margin-right:5px;
    margin-top:-2px;
}

.shipping_right > ul> li:first-child {
    border-bottom:1px solid#eee;
    padding-bottom:10px;
}

.shipping_right > ul> li> span {
    font-size:14px;
}

.shipping_day {
    font-size:14px;
    font-weight:700;
    color:#DC2832;
    margin-right:5px;
}

.shipping_probability {
    font-size:14px;
    font-weight:700;
}

.D_LFcard {
    width:525px;
    height:55px;
    display:flex;
    padding-top:15px;
    border-top:1px solid#eee;
}

.D_LFcard > img {
    width:23px;
    height:19px;
    margin-right:10px;
}

.D_LFcard > span {
    font-size:14px;
}

.D_LFpay {
    width:525px;
    height:51px;
    display:flex;
    padding-top:15px;
    border-top:1px solid#eee;
}

.D_LFpay > img {
    width:23px;
    height:19px;
    margin-right:12px;
}

.D_LFpay > img:nth-child(2) {
    width:50px;
    height:15px;
    margin-right:8px;
    margin-top:3px;
}

.D_LFpay > span {
    font-size:14px;
}

.D_map {
    width:525px;
    height:51px;
    display:flex;
    padding-top:15px;
    border-top:1px solid#eee;
}

.D_map > img {
    width:22px;
    height:24px;
    margin-right:10px;
    margin-top:-2px;
}

.D_map > span {
    font-size:14px;
}

.btn-primary {
    width:260px;
    height:55px;
    background-color:#fff;
    border-radius:4px;
    border:1px solid#dc2832;
    color:#dc2832;
    text-align:center;
    font-weight:600;
    font-size:14px;
    margin-top:60px;
}

.btn-outline-success {
    width:260px;
    height:55px;
    background-color:#dc2832;
    border-radius:4px;
    border:1px solid#dc2832;
    color:#fff;
    text-align:center;
    font-weight:600;
    font-size:14px;
    margin-top:60px;
}



/* 상세정보창 */

.detail_img {width:750px; margin:0 auto;}

.detail_img img{
    width:750px;
    margin-bottom:30px;
}

.nav-link {
    color:#495057 !important;
}


/* 반품/교환창 */

.detail_return > h3 {
    width:860px;
    font-size:18px;
    padding:18px 0 14px;
    margin:0 auto;
    padding-top:80px;
    font-weight:normal;
    border-bottom:1px solid #757575;
}

.detail_return table{
    width:860px;
    margin:0 auto;
}

.detail_return table > tr {
    border-bottom:1px solid#eee;
}

.detail_return table > tr > th {
    width:245px;
    padding:14px 44px 14px 20px;
    font-size:14px;
    background-color:#f7f7f7;
}

.detail_return table > tr > td {
    font-size:13px;
    padding:14px 44px 14px 43px;
}

.detail_return table > tr > td> img {
    margin-bottom:10px;
}


/* 배송창 */

.detail_shipping > h3{
    width:860px;
    font-size:18px;
    padding:18px 0 14px;
    margin:0 auto;
    padding-top:80px;
    font-weight:normal;
    border-bottom:1px solid #757575;
}

.detail_shipping table{
    width:860px;
    margin:0 auto;
}

.detail_shipping table > tr {
    border-bottom:1px solid#eee;
}

.detail_shipping table > tr > th {
    width:245px;
    padding:14px 44px 14px 20px;
    font-size:14px;
    background-color:#f7f7f7;
}

.detail_shipping table > tr > td {
    font-size:13px;
    padding:14px 44px 14px 43px;
}