.stBox:first-child {
    margin-left: 0;
}

.stBox {
    width:295px;
    height:295px;
    position:relative;
    float: left;
    margin-left:20px;
}

.stBox > img {
    width:100%;
    height:100%;
}

.stBox > span {
    position:absolute;
    z-index:10;
    bottom:10px;
    left:10px;
    color:#fff;
}

