.mySwiper3 {
    width: 1260px;
}

.mySwiper3 img:hover {
    cursor: pointer;
}

.mySwiper3 .swiper-button-next {
    margin-right: 20px;
}

.mySwiper3 .swiper-slide {
    width: 1260px;
    height: 995px;
}

.bfBox_top {
    float: left;
}

.bfBox_top img {
    width: 610px;
    margin-right: 20px;
}

.bfBox_bottom {
    float: left;
    margin-right: 20px;
    margin-top: 20px;
}

.bfBox_bottom img {
    width: 190px;
}

.bfBox_txt>h6:hover {
    text-decoration: underline;
    cursor: pointer;
}

.bfBox_txt > h6 {
    margin-top:10px;
    font-size: 13px;
    font-weight: 400;
}

.bfBox_txt > p:hover {
    text-decoration: underline;
    cursor: pointer;
}

.bfBox_txt > p {
    margin-top:-5px;
    font-size: 13px;
    font-weight: 400;
}

.bfBox_txt>span:nth-child(3) {
    font-size: 14px;
    color: red;
    margin-right: 10px;
    font-weight: bold;
}

.bfBox_txt>span:last-child {
    font-size: 14px;
    font-size: 13px;
    font-weight: bold;
}

:root {
    --swiper-theme-color: #000;
}