.Pickboxwrap {
    width:1120px;
    height:900px;
    margin:0 auto;
}

.Pickbox_wrap {
    float: left !important;
    width: 255px !important;
    height: 450px !important;
    margin-left: 20px !important;
}

.Pickbox_wrap > a >img {
    width:255px;
    height:348px;
    object-fit:cover;
}

.Pickbox_txt p:hover {
    text-decoration: underline;
    cursor: pointer;
}

.Pickbox_txt>p {
    font-size: 13px;
    margin-bottom: 0;
}

.Pickbox_txt>p:first-child {
    margin-top: 8px;
    font-size: 12px;
}

.Pickbox_txt>span:nth-child(3) {
    color: red;
    margin-right: 10px;
}

.Pickbox_txt>span:nth-child(4) {
    color: #ccc;
    font-size: 14px;
    text-decoration: line-through;
    margin-right: 10px;
}

.Pickbox_txt>span:last-child {
    font-size: 13px;
    font-weight: bold;
}